/* dispaly grid and positioning */
.display-grid {
  display: grid;
}

.justify-self-start {
  justify-self: start;
}
.justify-self-center {
  justify-self: center;
}
.justify-self-end {
  justify-self: end;
}

.justify-content-start {
  justify-content: start;
}
.justify-content-center {
  justify-content: center;
}
.justify-content-end {
  justify-content: end;
}

.align-self-flex-end {
  align-self: flex-end;
}
.align-self-flex-start {
  align-self: flex-start;
}
.text-align-left {
  text-align: left;
}
.text-align-right {
  text-align: right;
}

.letter-spacing-2 {
  letter-spacing: 2px;
}
.letter-spacing-3 {
  letter-spacing: 3px;
}
.letter-spacing-4 {
  letter-spacing: 4px;
}
.overflow-hidden {
  overflow: hidden;
}
.relative {
  position: relative;
}
.hidden {
  display: none;
}
.cursor-pointer {
  cursor: pointer;
}
.text-underline {
  text-decoration: underline;
}
.z-index-high {
  z-index: 9999;
}
.z-index-low {
  z-index: 1;
}

